@import "/src/common/styles/sass/variables";
@import "/src/common/styles/sass/mixins";

.skill {
    width: 180px;
    background-color: $ElemColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 10px 10px;
    padding: 10px 0;
    border-radius: 12px;

    .icon {
        height: 80px;
        width: 80px;

        & img {
            height: 100%;
            width: 100%;
        }
    }

    .skillTitle {
        @include text('Barlow',24px,600, 1.3, $textColor);
    }

    .description {
        @include text('Barlow',16px,500, 1.3, $textColor);
    }
}







